.card {
  border: 4px teal solid;
  border-radius: 2px;
  width: 150px;
  height: 150px;
  margin: 1rem;
  transition: all .2s ease-in-out;
  position: relative;
  box-shadow: 6px 6px 10px #999;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  background:rgb(255, 255, 255);
}


.card > .img-container > img {
  height: 100%;
}

.card:hover {

  box-shadow: 8px 8px 20px #999;
  transform: scale(1.1);
}

.card:active {
  transform: translate(1px, 1px);
}